import React, { FC, useMemo } from 'react';
import {
	ConstructorElement,
	CurrencyIcon,
	Button,
} from '@ya.praktikum/react-developer-burger-ui-components';
import { useNavigate } from 'react-router-dom';
import styles from './burger-constructor.module.css';
import Modal from '../modal/modal';
import DropTarget from '../drop-target/drop-target';
import { getOrder } from '../../services/slices/orderSlice';
import { openModal, closeModal } from '../../services/slices/modalSlice';
import { TIngredientWithID } from '../../services/types/data';
import OrderDetails from '../order-details/order-details';
import { useDispatch, useSelector } from '../../services/store';
import TotalPrice from '../total-price/total-price';
import SwipeDeleteBackground from './draggable-constructor-element/swipeable-draggable';

const BurgerConstructor: FC = () => {
	const dispatch = useDispatch();
	const { constructorIngredients, bun, isDragging } = useSelector((store) => {
		return store.constructorStore;
	});
	const { user } = useSelector((store) => store.user);
	const { type, isOpen } = useSelector((store) => store.modal);
	const navigate = useNavigate();

	const handleClick = () => {
		if (bun && constructorIngredients.length > 0) {
			if (user) {
				dispatch(getOrder([bun, ...constructorIngredients, bun]));
				dispatch(openModal({ type: 'ORDER_DETAILS' }));
			} else {
				navigate('/login');
			}
		}
	};

	const handleModalClose = () => {
		dispatch(closeModal());
	};

	const mainList = React.useMemo(() => {
		return constructorIngredients.map((ingredient: TIngredientWithID) => {
			const { id } = ingredient;

			return (
				<li className={styles.listItem} key={id}>
					<DropTarget id={id}>
						<SwipeDeleteBackground visible={!isDragging} {...ingredient} />
					</DropTarget>
				</li>
			);
		});
	}, [constructorIngredients]);

	return (
		<div className={styles.wrapper}>
			<div className={styles.constructorIngredients}>
				{bun ? (
					<div className={`${styles.constructorElementWrapper}`}>
						<DropTarget type="bun">
							<ConstructorElement
								type="top"
								isLocked
								text={`${bun.name} (верх)`}
								price={bun.price}
								thumbnail={bun.image}
								key={`${bun.id}_0`}
							/>
						</DropTarget>
					</div>
				) : (
					<DropTarget type="bun">
						<div className={`${styles.bunPlaceholder} ${styles.bunPlaceholderTop}`}>
							Выберите булки
						</div>
					</DropTarget>
				)}

				<ul className={`${styles.list} scrollable-list`}>
					{mainList.length ? (
						mainList
					) : (
						<li className={styles.listItem}>
							<DropTarget id={0}>
								<div className={styles.mainPlaceholder}>Выберите начинку</div>
							</DropTarget>
						</li>
					)}
				</ul>

				{bun ? (
					<div className={styles.constructorElementWrapper}>
						<DropTarget type="bun">
							<ConstructorElement
								type="bottom"
								isLocked
								text={`${bun.name} (низ)`}
								price={bun.price}
								thumbnail={bun.image}
								key={`${bun.id}_1`}
							/>
						</DropTarget>
					</div>
				) : (
					<DropTarget type="bun">
						<div className={`${styles.bunPlaceholder} ${styles.bunPlaceholderBottom}`}>
							Выберите булки
						</div>
					</DropTarget>
				)}
			</div>
			<div className={`${styles.orderSummary} mt-10`}>
				<span className="mr-10">
					<TotalPrice />
				</span>
				<Button
					htmlType="button"
					type="primary"
					size="medium"
					onClick={handleClick}
					data-testid="order-button"
					disabled={!bun || !constructorIngredients.length}
				>
					Оформить заказ
				</Button>

				{isOpen && type === 'ORDER_DETAILS' && (
					<Modal onClose={handleModalClose}>
						<OrderDetails />
					</Modal>
				)}
			</div>
		</div>
	);
};

export default BurgerConstructor;
